<template>
  <div>
    <table width="60%">
      <tbody>
        <tr>
                <td>
                  <button @click="spellHanzi">拼音</button>
                </td>
                <td>
                  <button @click="drawHanzi">笔画</button>
                </td>
                <td>
                  <button @click="juhuawen">菊花</button>
                </td>
                <td>
                  <button @click="reverse">翻转</button>
                </td>
                <td>
                  <button @click="jreverse">菊翻</button>
                </td>
              </tr>
              <tr>
                <td colspan="4">
                  <input type="text" v-model="hanzi" style="width:100%" />
                </td>
              </tr>
              <tr>
                <td colspan="4">
                  <textarea v-model="hzResult"  style="width:100%;height: 70px;"  />
                </td>
        </tr>
      </tbody>      
    </table>
  </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue';

const hanzi = ref('');
const hzResult = ref('');

function spellHanzi() {
  if (hanzi.value) {
    hzResult.value = cnchar.spell(hanzi.value);
  }
}

function drawHanzi(){
        if(document.getElementsByTagName("svg").length > 0){
           document.getElementsByTagName("svg")[0].parentNode?.parentNode?.removeChild(document.getElementsByTagName("svg")[0]?.parentNode)
        }

        const writer = cnchar.draw(hanzi.value, {
            type: cnchar.draw.TYPE.ANIMATION,
            animation: {
                autoAnimate: true,
            }
        });
        
        writer.drawNextStroke(()=>{
        // 当前笔画绘制完成的回调
        });
}

function juhuawen() {
				const inStr = hanzi.value;
				var code = String.fromCharCode(1161);
				var outStr = '';
				for(var i=0,l=inStr.length; i<l; i++){
					outStr += inStr.charAt(i) + code;
				}
				hzResult.value = outStr
}

function reverse(){
    var Str = hanzi.value.split('').reverse().join('');
    hzResult.value = Str;
}

function jreverse(){
  const inStr = hanzi.value;
				var code = String.fromCharCode(1161);
				var outStr = '';
				for(var i=0,l=inStr.length; i<l; i++){
					outStr += inStr.charAt(i) + code;
				}
				var Str = outStr.split('').reverse().join('');
    hzResult.value = Str;
}
</script>
